import styled from "styled-components";
import { Itinary } from "../pages/Itinaries";
import * as motion from "motion/react-client";
import { Card } from "./Card";

const Wrapper = styled(motion.div)`
  display: flex;
  flex-direction: column;
  flex-basis: 100%;
  padding: 2.8em;
  border-radius: 6px;

  h2 {
    margin: 0;
    font-size: 2.5em;
    padding-bottom: 0.6em;
    color: #ffb202;
  }

  & > div {
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 0;
    gap: 32px;

    @media (min-width: 768px) {
      padding: 0 2em;
      flex-direction: row;
      gap: 16px;
    }

    @media (min-width: 1280px) {
      gap: 32px;
    }

    .card {
      width: 100%;
      border-width: 8px;

      @media (min-width: 768px) {
        width: 50%;
      }
    }
  }
`;

type HighlightsProps = {
  itinaries: Itinary[];
};

export function Highlights({ itinaries }: HighlightsProps) {
  return (
    <Wrapper
      initial={{ opacity: 0, y: -50 }}
      animate={{ opacity: 1, y: 0 }}
      exit={{ opacity: 0, y: -50 }}
      className="animated-bg"
      layout
    >
      <h2> Incontournables</h2>

      <div>
        {itinaries.map((itinary) => (
          <Card key={itinary.id} {...itinary} />
        ))}
      </div>
    </Wrapper>
  );
}
