import * as motion from "motion/react-client";
import { Itinary } from "../pages/Itinaries";
import styled from "styled-components";

const Tags = {
  group: (
    <>
      <div></div>Groupe
    </>
  ),
  combined: (
    <>
      <div></div> Combin
    </>
  ),
};

const Wrapper = styled(motion.div)`
  position: relative;
  border: 1px solid var(--color-paper-darker);
  --cover-z-index: 10;
  width: 100%;

  @media (min-width: 768px) {
    width: calc(50% - 8px);
  }

  @media (min-width: 1280px) {
    width: calc(33% - 9px);
  }

  & .header {
    position: relative;
    width: 100%;
    padding-bottom: 62.5%;
  }

  & .cover {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: var(--cover-z-index);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
  }

  & .body {
    width: 100%;
    padding: 24px 16px 16px 16px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  & .title {
    color: var(--color-ink);
    font-size: 1.8rem;
    font-weight: 700;
    line-height: 2.4rem;
  }

  & .detail {
    color: var(--color-ink-light);
    font-size: 1.6rem;
    font-weight: 400;
    line-height: 2.4rem;
  }

  & .duration {
    margin-right: 4px;
  }

  & .price {
    padding-left: 4px;
    border-left: 1px solid var(--color-paper-darker);
  }

  & .priceValue {
    color: var(--color-blue);
    font-weight: bold;
  }
`;

const Meta = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1.2em;
`;

const Destination = styled.div`
  font-size: 1.4em;
  font-weight: 600;
  color: var(--color-orange);
`;

const Tag = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: var(--color-paper-darker);
  color: var(--color-ink);
  border-radius: 16px;
  padding: 0.5em 1em;
  gap: 0.5em;
`;

export function Card({
  id,
  picture,
  title,
  destination,
  price,
  duration,
  tag,
}: Itinary) {
  return (
    <Wrapper
      layout
      id={id.toString()}
      initial={{ opacity: 0, scale: 0 }}
      animate={{ opacity: 1, scale: 1 }}
      exit={{ opacity: 0, scale: 0 }}
      key="box"
      className="card"
      data-testid={`itinerary-card-${id}`}
      style={{ background: "white" }}
    >
      <div className="header">
        <div
          className="cover"
          style={{ backgroundImage: `url(${picture})` }}
        ></div>
      </div>
      <div className="body">
        <Meta>
          <Destination> {destination?.name}</Destination>
          {tag ? <Tag>{Tags[tag]}</Tag> : null}
        </Meta>
        <p className="title">{title}</p>
        <div className="detail">
          <span className="duration">{"Environ " + duration + " jours"}</span>
          <span className="price">
            {" partir de "}
            <span className="priceValue">{price}</span>
            {" "}
          </span>
        </div>
      </div>
    </Wrapper>
  );
}
