import { use } from "react";
import { Card } from "../components/Card";
import { fetchData } from "../api/fetchData";
import { API_ENDPOINTS } from "../config/api";
import { AnimatePresence } from "motion/react";
import { EmptyState } from "./EmptyState";
import { Highlights } from "../components/Highlights";

type Destination = {
  id: number;
  name: string;
};

export type Itinary = {
  id: number;
  picture: string;
  destination: Destination;
  title: string;
  price: number;
  duration: number;
  highlight: boolean;
  tag: "group" | "combined";
};

function searchItinary(itinary: Itinary, search: string): boolean {
  const searchableFields = [
    itinary.title,
    itinary.destination?.name,
    itinary.duration,
    itinary.price,
    itinary.tag,
  ];

  return searchableFields.some((value) =>
    Boolean(value?.toString().toLowerCase().includes(search))
  );
}

type ItinariesProps = {
  search: string;
};

export function Itinaries({ search }: ItinariesProps) {
  const data = use<Itinary[]>(fetchData<Itinary[]>(API_ENDPOINTS.itinaries));
  const filtered = search?.length
    ? data.filter((itinary) => searchItinary(itinary, search))
    : data;

  const highlights = filtered.filter((itinary) => itinary.highlight);
  const itinaries = filtered.filter((itinary) => !itinary.highlight);

  if (!filtered.length) {
    return <EmptyState />;
  }

  return (
    <div className="itinerary-container">
      <AnimatePresence mode="popLayout">
        {highlights.length ? <Highlights itinaries={highlights} /> : null}=
        {itinaries.map((itinary) => (
          <Card key={itinary.id} {...itinary} />
        ))}
      </AnimatePresence>
    </div>
  );
}
