let cache = new Map();

export function fetchData<T>(url: string) {
  // Caching logic is natively handled by Suspense-compatible
  // frameworks like Next.js or Relay but not in React Create App
  if (!cache.has(url)) {
    cache.set(url, getData<T>(url));
  }
  return cache.get(url);
}

async function getData<T>(url: string): Promise<T> {
  return await fetch(url).then((response) => response.json());
}
